/* Dark Theme Styles */

/* General Variables */
:root[data-theme="dark"] {
  --foreground-rgb: 255, 255, 255;
  --background-start-rgb: 10, 10, 10;
  --background-end-rgb: 0, 0, 0;
  
  /* Dark theme variables */
  --primary-color: #90caf9;
  --secondary-color: #f48fb1;
  --background-color: #121212;
  --background-alt-color: #121212;
  --paper-color: #1e1e1e;
  --text-primary: rgba(255, 255, 255, 0.87);
  --text-secondary: rgba(255, 255, 255, 0.6);
  --divider-color: rgba(255, 255, 255, 0.12);
  --error-color: #f44336;
  --success-color: #4caf50;
  --warning-color: #ff9800;
  --card-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  --card-border: 1px solid rgba(255, 255, 255, 0.08);
  --primary-color-rgb: 144, 202, 249;
  
  /* Sidebar specific */
  --sidebar-bg: #1c1c1c;
  --sidebar-item-hover: rgba(255, 255, 255, 0.08);
  --sidebar-item-active: rgba(100, 100, 100, 0.12);
  --sidebar-text: rgba(255, 255, 255, 0.87);
  --sidebar-icon: #aaa;
  --sidebar-border: rgba(255, 255, 255, 0.08);
}

/* Base Styles */
[data-theme="dark"] body {
  color: var(--text-primary);
  background: var(--background-color);
}

/* Main content styles */
[data-theme="dark"] main {
  background-color: var(--background-color);
}

/* Card and container styles */
[data-theme="dark"] .MuiCard-root, 
[data-theme="dark"] .MuiPaper-root,
[data-theme="dark"] .MuiContainer-root,
[data-theme="dark"] .MuiDrawer-paper {
  background-color: var(--paper-color) !important;
  color: var(--text-primary) !important;
}

/* Sidebar Styles */
[data-theme="dark"] .sidebar {
  background-color: var(--sidebar-bg) !important;
}

[data-theme="dark"] .MuiDrawer-paper {
  background-color: var(--sidebar-bg) !important;
  border-right: 1px solid var(--sidebar-border) !important;
  box-shadow: none !important;
}

[data-theme="dark"] .sidebar .MuiListItemButton-root {
  color: var(--sidebar-text);
  background-color: transparent;
  border-radius: 8px;
  margin: 4px 8px;
  outline: none !important;
  box-shadow: none !important;
  border: none !important;
}

[data-theme="dark"] .sidebar .MuiListItemButton-root:hover {
  background-color: var(--sidebar-item-hover) !important;
  color: #ccc !important;
  transform: none !important;
  transition: none !important;
  outline: none !important;
  box-shadow: none !important;
  border: none !important;
}

[data-theme="dark"] .sidebar .MuiListItemButton-root.active {
  background-color: var(--sidebar-item-active) !important;
  color: #ccc !important;
  font-weight: 500;
}

[data-theme="dark"] .sidebar .MuiListItemIcon-root {
  color: var(--sidebar-icon) !important;
  min-width: 40px;
}

[data-theme="dark"] .sidebar .MuiListItemText-primary {
  color: var(--sidebar-text) !important;
  font-weight: 500;
}

/* Sidebar Logo */
[data-theme="dark"] .sidebar .MuiToolbar-root .MuiTypography-root {
  font-weight: 700;
  color: #ccc !important;
  background: none !important;
  background-clip: initial !important;
  -webkit-background-clip: initial !important;
  -webkit-text-fill-color: initial !important;
  font-size: 1.5rem;
  letter-spacing: 0.5px;
}

/* Logout Button */
[data-theme="dark"] .sidebar .MuiListItemButton-root:last-child {
  margin-top: 16px;
  border-top: 1px solid var(--sidebar-border);
  border-radius: 0;
  padding-top: 16px;
}

[data-theme="dark"] .sidebar .MuiListItemButton-root:last-child .MuiListItemIcon-root {
  color: #ef5350 !important;
}

[data-theme="dark"] .sidebar .MuiListItemButton-root:last-child:hover {
  background-color: rgba(255, 255, 255, 0.05) !important;
  box-shadow: none !important;
  transform: none !important;
}

/* Fix for pseudo-elements that might be creating borders or outlines */
[data-theme="dark"] .sidebar *::before,
[data-theme="dark"] .sidebar *::after,
[data-theme="dark"] .MuiDrawer-paper *::before,
[data-theme="dark"] .MuiDrawer-paper *::after {
  display: none !important;
  content: none !important;
  opacity: 0 !important;
  border: none !important;
  outline: none !important;
  box-shadow: none !important;
}

/* Fix for Touch and Focus Effects */
[data-theme="dark"] .sidebar *,
[data-theme="dark"] .MuiDrawer-paper * {
  outline: none !important;
  -webkit-tap-highlight-color: transparent !important;
  transition: none !important;
  animation: none !important;
}

/* Main Background Colors */
[data-theme="dark"] .dashboard-layout {
  background-color: var(--background-color);
}

/* Content Area */
[data-theme="dark"] .main-content {
  background-color: var(--background-color);
}

/* Fix for Content Wrapper */
[data-theme="dark"] .content-wrapper {
  background-color: var(--background-color);
}

/* Fix for any MUI component states */
[data-theme="dark"] .MuiButtonBase-root:hover {
  background-color: rgba(255, 255, 255, 0.08) !important;
}

/* Remove Ripple Effects */
[data-theme="dark"] .MuiTouchRipple-root {
  display: none !important;
} 